<template>
	<!-- 传入show,返回清单切换类型 -->
	<view class="sideBar">
		<u-popup :show="show" @close="close"  mode="left">
		        <view >
					<!-- 侧边弹窗内容 -->
					<view class="over">
						<view class="top flex align-center">
							<!-- 头像 -->
							<u--image src="/static/c1.png" shape="circle" width="80px" height="80px" ></u--image>
							<!-- 昵称 -->
							<text class="text-white text-xl" @click="toLogin">立即登录</text>
						</view>
						<!-- 清单列表 -->
						<view class="list  text-lg">
							<view v-for="(item,index) in sideList" :key="index" class="item" @click="$emit('clickList',index)">
								<i :class="item.icon"></i>
								<text class="margin-left-xs">{{item.name}}</text>
							</view>
						
						</view>
						
						<!-- 新增清单 -->
					</view>
		        </view>
		</u-popup>
		
	</view>
</template>

<script>
	export default {
		name:"sideBar",
		data() {
			return {
				
			};
		},
		props:['show1','sideList'],
		computed:{
			show() {
				return this.show1;
			}
		},
		methods: {
			close() {
			  this.$emit("update:show1",false);
			},
			toLogin() {
				uni.navigateTo({
					url:'../user/login'
				})
			}
		}
	}
</script>

<style lang="scss">
	
	.sideBar {
		
		// 侧边弹窗宽度
		.over {
			width: 500rpx;
		}
		// 侧边弹窗顶部
		.top {
			width: 100%;
			height: 20vh;
			background-color: #ff5566;
		}
		// 清单
		
		.item {
			height: 7vh;
			display: flex;
			align-items: center;
			margin-left: 30rpx;
		}
	}
</style>